import React from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import s from "./FormForAuth.module.scss";
import { Link } from "react-router-dom";

function FormForAuth({ error, handleLogin, userData, setUserData }) {
  return (
    <div className={s.auth}>
      {error && <p className="error">{error}</p>}

      <Form onSubmit={handleLogin}>
        <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
          <Form.Label>Email address</Form.Label>
          <Form.Control
            type="email"
            placeholder="name@example.com"
            required
            value={userData.email}
            onChange={(e) => setUserData({ ...userData, email: e.target.value })}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            placeholder="Пароль"
            required
            value={userData.password}
            onChange={(e) => setUserData({ ...userData, password: e.target.value })}
          />
        </Form.Group>
        <Button variant="secondary" type="submit">
          Авторизация
        </Button>
        <p className={s.auth__registr}>Вы еще не зарегистрированны? - </p>
        <Link className={s.auth__link} to="/registr">
          Регистрация
        </Link>
      </Form>
    </div>
  );
}
export default FormForAuth;
